import axios from 'axios'

const API_KEY = 'f0e68a20431b78c55e111178b88128fc'
const WEATHER_ROOT_URL = `https://api.openweathermap.org/data/2.5/weather?appid=${API_KEY}`
const IP_ROOT_URL = `https://ipapi.co/json`

export const fetchWeather = (response) => {
    return {
        type: "FETCH_WEATHER",
        payload: response
    }
}

export const asyncFetchWeatherByCityName = (city) => {
    return dispatch => {
        const url = `${WEATHER_ROOT_URL}&q=${city}`
        axios.get(url)
        .then(function(response){
            dispatch(fetchWeather(response))
        })
        .catch(function(error) {
            console.log(error)
        })
    }
}

export const asyncFetchWeatherByLocation = (lat, lon) => {
    return dispatch => {
        const url = `${WEATHER_ROOT_URL}&lat=${lat}&lon=${lon}`
        axios.get(url)
        .then(function(response){
            dispatch(fetchWeather(response))
        })
        .catch(function(error) {
            console.log(error)
        })
    }
}

export const asyncFetchLocation = () => {
    return dispatch => {
        axios.get(IP_ROOT_URL)
        .then( response => {
            let lat = response.data.latitude
            let lon = response.data.longitude
            dispatch(asyncFetchWeatherByLocation(lat, lon))
        })
        .catch( error => {
            console.log(error)
        })
    }
}


