import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { asyncFetchLocation } from '../actions/'
import { asyncFetchWeatherByLocation } from '../actions/'

class Weather extends Component {

    componentWillMount() {
        this.props.asyncFetchLocation()
    }

    render() {
        let weather
        if(this.props.weather) {
            console.log(this.props.weather)
            weather = this.props.weather
            return(
                <div>
                    Tempreature of <b>{weather.name}</b> in <b>{weather.sys.country}</b> is 
                    <b> {weather.main.temp - 273.3}</b>
                </div>
            )
        }
        else {
            return (
                <p>Search for a city</p>
            )
        }
    }
}

function mapStateToProps(state) {
    return({
        weather: state.weather.weather
    })
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        asyncFetchLocation: asyncFetchLocation,
        asyncFetchWeatherByLocation: asyncFetchWeatherByLocation
    }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(Weather)


